      </template>
    </el-table-column>
    <el-table-column label="">
      <el-button type="danger" size="mini"></el-button>
    </el-table-column>
  </el-table>
</div>
</template>

<script type="text/ecmascript-6">
export default {
  name: "MemberList",
  data() {
    return {
      user_list: []
    }
  },
  components: {},
  mounted(){
    this.$http.getUserList().then(res => {
      this.user_list = res.data
    })
  }
}
</script>

